.hidden-menu {
  display: block;
  position: absolute;
  padding: 10px;
  padding-top: 0;
  margin: 0 auto;
  box-sizing: border-box;
  width: 228px;
  background-color: white;
  height: 648px;
  top: 0;
  left: -228px;
  transition: left .2s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.hidden-menu__site-list {
  list-style:none;
  padding: 0;
  margin: 0;
}

.menu-description {
  list-style:none;
  padding: 0;
  margin: 0;
  margin-left: 15px;
  margin-top: 40px;
  margin-bottom: 25px;
}

.hidden-menu-ticker {
  display: none;
}

.menu-description__item {
  display: flex;
}

.menu-description__item img {
  margin-right: 15px;
}

.menu-description__item p {
  margin: 0;
  font-size: 14px;
  font-weight: lighter;
  margin-bottom: 32px;
}

.menu-description__item--bottom a {
  font-weight: bold;
  margin-bottom: 5px;
  text-decoration: none;
  color: black;
}

.hidden-menu__button {
  font-size: 14px;
  font-weight: lighter;
  padding: 8px 30px 10px 30px;
  border: 2px solid #f83659;
  border-radius: 50px;
  margin-left: 13px;
}

.btn-menu {
  padding: 5px;
  position: absolute;
  top: 5px;
  right: 15px;
  cursor: pointer;
  transition: right .23s;
  z-index: 6;
  width: 25px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.btn-menu span {
  display: block;
  height: 2px;
  background-color: #ffffff;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.btn-menu span.first {
  margin-top: 15px;
}

.hidden-menu-ticker:checked ~ .btn-menu {
  right: 15px;
}

.hidden-menu-ticker:checked ~ .hidden-menu {
  left: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu span.first {
  transform: rotate(45deg);
  top: 10px;
  background-color: #ffffff;
}

.hidden-menu-ticker:checked ~ .btn-menu span.second {
  opacity: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu span.third {
  transform: rotate(-45deg);
  top: -4px;
  width: 25px;
  background-color: #ffffff;
  margin-left: 0;
}


@media (min-width: $desktop-width) {
  .hidden-menu-wrapper {
    display: none;
  }
}
