.page-header {
  margin: 0 auto;
  width: 100%;
  color: black;
}

.page-header__wrapper {
  position: relative;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid white;
  padding-bottom: 13px;
  z-index: 1;
}

.page-header__phone {
  margin-top: 12px;
  z-index: 1;
}

.page-header__nav {
  z-index: 1;
}

