.schema__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 38px;
}

.schema__header {
  margin: 0;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
}

.schema__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 41px;

}

.schema__item {
  position: relative;
  width: 290px;
  height: 552px;
  margin-left: 14px;
  margin-bottom: 18px;
}

.schema__item img {
  margin-left: 16px;
}

.schema__item1 {
  background-image: url("../img/bg3.png");
  background-repeat: no-repeat;
}

.schema__item2 {
  background-image: url("../img/бг4.png");
  background-repeat: no-repeat;
}

.schema-header {
  margin-top: 20px;
  margin-bottom: 30px;
  margin-left: 15px;
  color: white;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  width: 235px;
}

.schema-header--line::before {
  content: " ";
  position: absolute;
  top: 355px;
  width: 70px;
  height: 4px;
  background-color: #dddddd;
}

.schema-header--before::before {
  content: " ";
  position: absolute;
  top: 333px;
  width: 70px;
  height: 4px;
  background-color: #dddddd;
}

.schema-description {
  margin-left: 15px;
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: lighter;
  width: 258px;
  margin-bottom: 25px;
}

.schema-header2 {
  width: 208px;
}

.schema-description2,
.schema-header2 {
  color: black;
}
