.form__application {
  width: 100%;
  margin: 0 auto;
  background-image: url("../img/191106AManWorkingAtAComputerWithASimOriginal.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.application__wrapper {
  width: 320px;
  margin: 0 auto;
  text-align: center;

}

.application__header {
  margin: 0;
  letter-spacing: 1px;
  padding-top: 25px;
  font-size: 26px;
  font-weight: bold;
  color: white;
}

.application__description {
  margin-top: 10px;
  font-size: 15px;
  font-weight: lighter;
  letter-spacing: 0.4px;
  color: white;
}

.application__item {
  width: 230px;
  display: flex;
  justify-content: space-between;
  margin-left: 48px;
}
.application__item a {
  font-weight: bold;
  font-size: 20px;
  color: white;
  margin: 0;
  letter-spacing: 1px;
  text-decoration: none;
}

.form {
  width: 320px;
}

.form__wrapper {
  margin-top: 21px;
}

.form__item {
  position: relative;
  padding-bottom: 7px;
  list-style: none;
}

.form__item--1::before {
  content: " ";
  position: absolute;
  top: 14px;
  left: 36px;
  width: 16px;
  height: 16px;
  background-image: url("../img/name.svg");
}

.form__item--2::before {
  content: " ";
  position: absolute;
  top: 13px;
  left: 34px;
  width: 19px;
  height: 18.97px;
  background-image: url("../img/phone.svg");
}

.form__item--3::before {
  content: " ";
  position: absolute;
  top: 15px;
  left: 33px;
  width: 19px;
  height: 14px;
  background-image: url("../img/email.svg");
}


.form__list {
  padding: 0;
  margin: 0;
}

.form__window {
  padding-top: 16px;
  padding-bottom: 17px;
  padding-left: 58px;
  width: 240px;
  border-radius: 6px;
  border: none;
  font-size: 15px;
  font-weight: lighter;

  &--message {
    padding-left: 19px;
    padding-bottom: 34px;
    width: 279px;
  }
}

.form__button-container {
  padding-bottom: 31px;
}

.form__button {
  border: none;
  background-color: #f83659;
  color: white;
  padding: 21px 89px 20px 90px;
  border-radius: 50px;
  cursor: pointer;
  font-weight: lighter;
  font-size: 16px;
  letter-spacing: 1.28px;
}

.form__information {
  width: 295px;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 25px;
  padding-top: 12px;
}

.form-list__value {
  padding-left: 56px;
  font-size: 15px;
  text-align: left;
  color: white;
  font-weight: lighter;
}

.form__information::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 11px;
  width: 25px;
  height: 25px;
  background-color: white;
  border-radius: 4px;
}

.form-list__check-box {
  content: "";
  position: absolute;
  top: 22px;
  left: 17px;
  display: none;
  width: 21px;
  height: 16px;
  background-image: url("../img/галка.svg");
  background-repeat: no-repeat;
}

.form-list__window:checked + .form-list__check-box {
  display: block;
}
