.services {
  margin-top: 28px;
}

.services__header {
  width: 320px;
  margin: 0 auto;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 28px;
}

.services__list {
  padding: 0;
  width: 320px;
  margin: 0 auto;
  color: white;
  list-style:none;
  padding-top: 23px;
  margin-left: 15px;
}

.services__wrapper {
  background-color: #0055b2;
}

.services__background {
  background-image: url("../img/Векторный смарт-объект.png");
  background-repeat: no-repeat;
  margin: 0 auto;
  width: 320px;
  padding-bottom: 12px;
}

.services__item {
  width: 182px;
  font-size: 14px;
  font-weight: lighter;
  line-height: 18px;
  letter-spacing: 0.84px;
  margin-bottom: 17px;
}

.services__arrow-white {
  margin-top: 4px;
  margin-left: 15px;
  margin-bottom: 25px;
}

.services__list-two {
  padding: 0;
  width: 320px;
  margin: 0 auto;
  color: black;
  list-style:none;
  display: flex;
  flex-wrap: wrap;
}

.services__item-two {
  width: 110px;
  height: 80px;
  padding-right: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 14px;
  margin-top: 20px;

}
