.start {
  background-image: url("../img/бг1.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 576px;
  margin-top: -86px;
  padding-top: 86px;
  color: white;
}

.start__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-top: 75px;
  text-align: center;
}

.start__header {
  width: 283px;
  margin-left: 20px;
  font-size: 29px;
  line-height: 35px;
  margin-bottom: 19px;
}

.start__description {
  width: 260px;
  margin-left: 31px;
  font-size: 17px;
  font-weight: lighter;
  line-height: 25px;
  margin-bottom: 40px;
}

.start__button {
  margin-bottom: 55px;
  width: 253px;
  margin-left: 34px;
  margin-right: 33px;
  font-size: 16px;
  font-weight: lighter;
  color: white;
  background-color: #f83659;
  border: none;
  padding: 21px 81px 20px 81px;
  border-radius: 50px;
}

.start__number span{
  position: relative;
  font-size: 14px;
  font-weight: lighter;
  letter-spacing: 1.12px;
}

.start__number--color::before {
  content: " ";
  position: absolute;
  top: 23px;
  left: -4px;
  width: 24px;
  height: 3px;
  background-color: #b7babf;
}

.start__number-two::before {
  content: " ";
  position: absolute;
  top: 23px;
  left: -4px;
  width: 24px;
  height: 3px;
  background-color: #f83659;
}

.start__number-one {
  margin-right: 26px;
}

.start__number-two {
  margin-right: 23px;
  color: #f83659;
}
