.advantages__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-top: 78px;
}

.advantages__header {
  margin: 0;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 52px;
  text-align: center;
}

.advantages__item {
  width: 263px;
  margin-left: 28px;
  margin-bottom: 44px;
}

.advantages__item img {
  background-color: white;
  padding: 30px;
  border-radius: 56px;
  box-shadow: 11px 12px 44px -14px aquamarine;
}

.advantages__item-header {
  position: relative;
  margin-top: 29px;
  margin-bottom: 38px;
  font-size: 18px;
  line-height: 20px;
}

.advantages__item-header::before {
  content: " ";
  position: absolute;
  top: 33px;
  left: 97px;
  width: 70px;
  height: 4px;
  background-color: #dddddd;
}

.advantages__item-description {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: lighter;
}
