.contacts__wrapper {
  width: 320px;
  margin: 0 auto;
  text-align: center;
}

.contacts__header {
  margin-top: 35px;
  margin-bottom: 47px;
  font-weight: bold;
  font-size: 26px;
}

.contacts__list {
  list-style: none;
  margin-bottom: 19px;
  padding: 0;
}

.contacts__item-header {
  margin: 0;
  margin-bottom: 16px;
  font-weight: lighter;
  font-size: 15px;
  color: rgb(134, 134, 134);
}

.contacts__description {
  margin-left: 15px;
  width: 290px;
  display: flex;
  justify-content: space-between;

  &--1 {
    width: 300px;
  }

  &--3 {
    width: 195px;
    margin-left: 64px;
  }
}

.contacts__description p {
  margin: 0;
  margin-bottom: 13px;
  font-weight: lighter;
  font-size: 14px;
  color: black;
}

.contacts__description--2 a {
  font-weight: bold;
  text-decoration: none;
  color: black;
  margin-bottom: 13px;
  font-size: 14px;
}

.contacts__button--1 {
  font-size: 14px;
  font-weight: lighter;
  padding: 13px 62px 12px 61px;
  border: 2px solid #f83659;
  border-radius: 50px;
  margin-left: 13px;
  margin-bottom: 39px;
}

.contacts__button--2 {
  font-size: 14px;
  font-weight: lighter;
  padding: 8px 30px 10px 30px;
  border: 2px solid #f83659;
  border-radius: 50px;
  margin-left: 13px;
  margin-bottom: 42px;
}

.contacts__social {
  padding: 0;
  margin-left: 31px;
  width: 260px;
  list-style: none;
  display: flex;
  justify-content: space-between;
}
